
.App-header {
    background: rgb(0,0,0);
    background: linear-gradient(333deg, rgba(0,0,0,1) 6%, rgb(8, 105, 134) 100%);
    font-family: monospace;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(9px + 2vmin);
    font-weight: bolder;
    color: white;
}
.App-header p {
    font-size: 0.9rem;
    font-weight: lighter;
    opacity: 0.7;
}

#editor-container {
    width: 100%;
    max-width: 700px;
    float: right;
}

#editor {
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 0.7rem;
    padding: 1.4rem;
    width: 100%;
    height: 72vh;
    background: rgb(0,0,0);
    background: linear-gradient(156deg, rgb(10, 23, 28) 21%, rgb(32, 83, 96) 90%, rgb(129, 129, 129) 100%);
    color: white;
}
#editor:hover {
    background-color: #262626;
}

#preview-container {
    max-width: 700px;
    width: 100%;
    float: left;
}

#preview {
    border: 2px solid lightgray;
    font-size: 0.8rem;
    padding: 1.2rem;
    overflow-y: auto;
    height: 79vh;
}

#preview pre {
    border: 1px solid lightgray;
    padding: 7px;
    background-color: white;
}
#preview td,
#preview th {
    border: 1px solid #224b4b;
    padding:6px;
}
#preview table {
    margin-bottom: 20px;
}
#preview img {
    max-width: 20vh;
    padding: 20px;
}

#button-bar {
    border-radius: 10px;
    border: 1px solid lightgray;
    background: rgb(0,0,0);
    background: linear-gradient(333deg, rgba(0,0,0,1) 6%, rgb(21, 53, 56) 100%);
    height: 60px;
    padding: 5px;
    width:100%;
    text-align: right;
    float: right;
    max-width: 800px;
}

#button-bar button {
    margin: 5px;
}

.sub-heading {
    background: rgb(0,0,0);
    background: linear-gradient(333deg, rgb(20, 42, 42) 6%, rgb(24, 99, 117) 100%);
    border-radius: 17px;
    font-size: 1rem;
    text-align: center;
    color: #f3f3f3;
    padding: 4px;
}